import Vue from 'vue';
import VueResource from 'vue-resource';
import logger from './logger';
import { getStorage } from './local_storage';

let inited = false;
const CSRF_KEY = 'CSRFPreventionToken';
const NO_AUTHORIZATION = 401;

/**
 *  vue-resource 
 */
export function init () {
    if (inited) {
        return;
    }
    inited = true;

    Vue.use(VueResource);
    Vue.http.options.emulateJSON = true;

    // /bbc
    // 1this.$http.get('/test/url') root
    // 2this.$http.get('test/url') root /bbc/test/url
    Vue.http.options.root = '/bbc';

    // csrf
    Vue.http.headers.common[CSRF_KEY] = getCSRF();

    // hookjson
    Vue.http.interceptors.push((request, next) => {

        //  / 
        request.url = fixURL(request.url);

        // json
        next((response) => {

            // waf
            if (checkReLogin(response, request.url)) {
                reLogin();
                response.ok = true;
                response.jsonData = {
                    success: true,
                    msg: response.statusText
                };
                return response;
            }

            // content-typeresponseTypeapplication/xml
            if (!response.bodyText && typeof Blob !== 'undefined' && response.body instanceof Blob) {

                response.bodyText = new Promise((resolve) => {
                    let reader = new FileReader();

                    reader.readAsText(response.body);
                    reader.onload = () => {
                        resolve(reader.result);
                    };

                });
            }

            return jsonResponse(response, request);
        });
    });

}

function getCSRF () {
    if (process.env.NODE_ENV === 'development' && window.BBC) {
        window.BBC.CSRF = getStorage(CSRF_KEY);
    }
    return window.BBC && window.BBC.CSRF || 'token';
}

/**
 * json
 * @param {Object} response 
 * @param {Object} request  
 * @returns {Promise}       Promise
 */
function jsonResponse (response, request) {
    return response.json().then((jsonData) => {

        // json
        logger.log('[ajax] [response==>json] success: %o. %o', response, jsonData);

        // jsonData
        response.jsonData = jsonData;

        // success
        // reject
        if (jsonData.success === false || jsonData.success === 0 || jsonData.success === 'false') {

            logger.warn('[ajax] [response==>json] jsonData.success: false', response, jsonData);

            // vue-resourceresolve, reject
            //  function Http (options) {} 
            response.ok = false;

            if (checkReLogin(response, request.url, jsonData)) {
                reLogin();
            }
        }
        return response;

    }, (error) => {

        // json
        logger.warn('[ajax] [response==>json] error: %o. %o', response, error);

        // jsonData
        response.jsonData = {
            success: false,
            msg: response.body || response.data || _('')
        };
        return response;
    });
}

/**
 * BBC URL
 * staticlogin / 
 * @param {String} url  url
 * @returns {string}    url
 */
function fixURL (url = '') {
    let ignoreList = [
        /^\/static/,
        /^login$/,
        /^\/cluster/
    ];

    for (let i = 0; i < ignoreList.length; i++) {
        let ignoreReg = ignoreList[i];
        if (ignoreReg.test(url)) {
            return url;
        }
    }

    if (url.slice(-1) !== '/' && url.indexOf('?') === -1) {
        url = url + '/';
    }

    return url;
}

/**
 * 
 * @param {Object} res      response 
 * @param {String} url      url
 * @param {Object} jsonData 
 * @returns {boolean}       401
 */
function checkReLogin (res, url, jsonData) {

    // cluster
    if (/^\/cluster/.test(url)) {
        return false;
    }

    // 401
    if (res && !res.ok && res.status === NO_AUTHORIZATION) {
        return true;
    }
    if (jsonData && !jsonData.success && jsonData.no_auth) {
        return true;
    }
    return false;
}

/**
 * 
 */
function reLogin () {
    if (process.env.NODE_ENV === 'development') {
        window.location.href = './login.html';
    } else {
        window.location.href = './login';
    }
}



